<template lang="pug">
    section(class="about")
        div(class="container")
            div(class="title")
                h2 {{ AboutData.choose_us.title }}
            div(class="about-content")
                .about-text
                    p {{ AboutData.choose_us.body }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "WhyChoose",
    mounted() {
        this.$store.dispatch("getAboutData");
    },
    computed: {
        ...mapState(["AboutData"]),
    },
};
</script>
