<template lang="pug">
    section(class="team")
        div(class="container")
            div(class="title")
                h2 Our 
                    span team
            div(class="team-content")
                div(class="row")
                    div(class="col-md-6 col-lg-3" v-for="team in AboutData.our_team", :key="team.id")
                        div(class="team-box wow fadeInUp" data-wow-duration="1s")
                            div(class="team-image")
                                img(:src="team.image", alt="")
                            div(class="team-text")
                                h3 {{ team.title }}
                                p {{ team.body }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "OurTeam",
    mounted() {
        this.$store.dispatch("getAboutData");
    },
    computed: {
        ...mapState(["AboutData"]),
    },
};
</script>
