<template lang="pug">
    section(class="market")
        div(class="container")
            div(class="title")
                h2 {{ AboutData.market_exception.title }}
            div(class="market-content")
                .row 
                    .col-lg-7
                        .market-text
                            p {{ AboutData.market_exception.body }}
                    .col-lg-5
                        .market-image
                            img(:src="AboutData.market_exception.image", alt="")
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "MarketException",
    mounted() {
        this.$store.dispatch("getAboutData");
    },
    computed: {
        ...mapState(["AboutData"]),
    },
};
</script>
